import '../css/musicplayer.css'
import * as m from "@magenta/music"
import { useState } from 'react';

const music = new m.MusicVAE("https://storage.googleapis.com/magentadata/js/checkpoints/music_vae/trio_4bar")
music.initialize()
const PLAYER = new m.Player()
// const qns = m.sequences.quantizeNoteSequence(TWINKLE_TWINKLE, 4);


const MusicPlayer = ({mood}) => {
    const [status,setStatus] =useState("Click to Play!")
    const tooglePlay=()=>{
        const disc = document.getElementById("disc");
        
        if (disc.classList.contains("image")){
            setStatus("Click to Play!")
            PLAYER.stop()
            return disc.classList.remove("image")
        }
        
        music.sample(1,0.5)
        .then((sample)=>{
            setStatus(mood?mood:"Random")
            PLAYER.start(sample[0])
            disc.classList.add("image")
        })
    
    }

    return ( 
        <div className="musicplayer_" 
        onClick={()=>{
            setStatus("Loading Model (18MB)");
            tooglePlay()}}>
            <div className="disc" id="disc" >
            </div>
            <div className="display">
                <div className="heading">
                    {status}
                </div>
                <div className="composer">
                    "Music by Riddim Ai" 
                </div>
            
            </div>
        
        </div>
     );
}
 
export default MusicPlayer;